<template>
  <div class="text-[12px] h-screen w-screen bg-gray-200 p-1 overflow-auto">
    <titleheader :goRoute="() => router.push({ path: '/classRoom' })" title="话题广场"></titleheader>
    <!-- // 搜索框 -->
    <div class="bg-white-500 flex justify-between items-center">
      <!-- <van-icon name="user-o" class="" size="0.65rem" /> -->
      <van-search v-model="value" shape="round" background="transparent" @click="search" class="flex-1"
        placeholder="请输入搜索关键词">
        <template #right-icon>
          <span class="text-green-500">
            搜索
          </span>
        </template>
      </van-search>
    </div>

    <div @click="goTopicDetail(index)" v-for="(value, index) in filterTopicData" :key="value.title"
      class="w-full bg-white rounded p-1 mb-1">
      <div class="flex items-center">
        <van-icon v-if="value.official" name="wap-nav" size="0.5rem"></van-icon>
        <span class="font-black text-[15px] mx-1">
          {{ value.title }}
        </span>
        <div class="text-white p-0.5 bg-orange-500 rounded-xs">官方</div>
      </div>
      <div class=" my-1 flex justify-between pb-2 border-b border-gray-200">
        <div class="w-[50%]">
          {{ value.detail }}
        </div>
        <Image class="rounded" :src="value.image"></Image>
      </div>
      <div class="text-gray-500 flex justify-between">
        <span>2025-06-05 09:06:07</span>
        <span>{{ value.taolun }}讨论 | {{ value.dianzan }}点赞</span>
      </div>
    </div>
  </div>
</template>
<script setup lang='ts'>
import titleheader from '@/components/titleHeader.vue'
import { topicDetail as TopicDetail } from '@/store/topicDetail'
const router = useRouter()
const topicData = ref([{
  title: '食谱',
  detail: '营养均衡,美味相伴!快来发送你的班级食谱吧！',
  image: '/public/image/食谱.jpg',
  time: '2025-06-07 09:06:07',
  taolun: 0,
  dianzan: 0,
  official: true,
}, {
  title: '寻找最美班级',
  detail: '一起"晒"出班级精彩，留下班级成长记忆！',
  image: '/public/image/食谱.jpg',
  time: '2025-06-07 09:06:07',
  taolun: 0,
  dianzan: 0,
  official: true
}])
const topicDetail = TopicDetail()
const goTopicDetail = (index: number) => {
  topicDetail.topicDetail = topicData.value[index]
  router.push({ path: '/topicDetail' })
}
const filterTopicData = ref<Array<any>>([])
const value = ref('')
onMounted(() => {
  filterTopicData.value = topicData.value
})
const search = () => {
  filterTopicData.value = topicData.value.filter((value1) => {
    if (!value1) {
      filterTopicData.value = topicData.value
    }
    if (value1.title.search(value.value) != -1) {
      return true
    }
    else {
      return false
    }
  })
  console.log(filterTopicData.value)
}
</script>
